body{
  background-image: url("./StaticImages/background.jpg");
  background-size: fit;
  /* background-position-y: 45%; */
  background-position: right bottom;
  background-repeat: no-repeat;
  font-family: 'Oxygen', sans-serif; 
  /* background-color: rgb(184, 156, 45); */
  height: 100vh;
}



/* LOGIN FORM */
.login-form{
  background-color: rgba(184, 156, 45, 0.3);
  padding: 20px;
  border-radius: 10px;
  margin-top: 100px;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.login-form > button{
align-self: flex-end;
}

.login-form label{
  width: 100%;
  background: transparent;
  /* background: rgba(184, 156, 45, 0.3); */
}

.login-page{
  max-height: 100vh;
  min-height: 100vh;
}

.login-form p{
  width: 100%;
  background: transparent;
  /* background: rgba(184, 156, 45, 0.3);  */
}

span{
  background: inherit;
}


/* REGISTER FORM */
.register-form{
  background-color: rgba(184, 156, 45, 0.3);
  padding: 10px;
  border-radius: 10px;
  margin-top: 80px;
  width: inherit;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.register-form > button{
  align-self: flex-end;
  }
  
.register-form label{
  width: 100%;
  background: transparent;
  /* background: rgba(184, 156, 45, 0.3); */
}
  
.register-page{
  max-height: 100vh;
  min-height: 100vh;
}

.register-form p{
  width: 100%;
  background: transparent;
  /* background: rgba(184, 156, 45, 0.3);  */
}

.split-register{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* SPLASH MODAL STYLING */

.new-well-form{
  background-color: rgba(184, 156, 45, 0.3);
  padding: 10px;
  border-radius: 10px;
  /* margin-top: 10px; */
  width: inherit;
  align-self: center;
  display: flex;
  flex-direction: column;
}


.form-container{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  height: auto;
}

.welcome-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(241, 241, 241, 1.0);;
  padding: 20px;
  border-radius: 10px;
  /* margin-top: 35vh; */
  width: 35vw;
  margin-left: auto;
  margin-right: auto;
}

.fp-h1 {
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  font-size: 36px;
  margin-bottom: 16px;
  background-color: transparent;
}

.fp-p {
  font-family: 'Oxygen', sans-serif; 
}

.welcome-page-log-button{
  background-color: rgba(184, 156, 45, 0.6);
  padding: 2px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  transition: all .2s ease-in-out; 
}

.welcome-page-log-button:hover{
  background-color: rgba(184, 156, 45, 0.8);
  transform: scale(1.1); 
}

.well-container{
  display: flex;
  position: relative;
}

.well-info{
  max-height: 100vh;
  width: 75vw;
  border: 1px solid black;
  background-color: rgba(241, 241, 241, 0.2);
  padding: 16px;
}


.well-table-top{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

.text-right{
  text-align: right;
}

h2{
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  font-size: 36px;
  margin-bottom: 16px;
}

.perf-col, .casing-col, .cement-col, .hole-col, .plug-col{
  height: 70vh;
  max-height: 70vh; 
  
  background-color: rgba(241, 241, 241, 1.0);
  border-right: 1px black solid;
}

.plug-col{
  border-right: none;
}

.well-hole, .well-casing, .well-cement, .well-plug, .well-perfs{
  padding: 16px;
}

.scroll-area{
  overflow: hidden;
  overflow-y: scroll;
  height: 55vh;
  max-height: 55vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#new-well{
  display: flex;
  align-items: center;
  justify-content: center;
}


.canvas-frame{
  width: 25vw;
  height: 100vh;
  border: 3px solid purple;
  background-color: rgba(241, 241, 241, 1.0);
  z-index: 5;
}

.plat-pop-up{
  position: absolute;
  top: 5%;
  left: 32.5%;
  width: 500px !important;
  height: 500px;
  display: none;
  z-index: 99;
  border: 15px green solid;
  background-color: rgba(241, 241, 241, 1.0);
}

.well-list-container{
  width: 100%;
  border: thin solid black;
  padding: 16px;
  border-radius: 16px;
}

.well-ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.icon{
  cursor: pointer
}

.ListItem{
  background-color: rgba(184, 156, 45, 0.4);
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 8px;
  padding-bottom: 0px;
  justify-content: space-between;
}

li:nth-child(even){
  background-color: rgba(184, 156, 45, 0.2);
}

.li-button{
  display: inline;
  border-radius: 8px;
  background-color: rgba(241, 241, 241, 1.0);
  transform: scale(0.9); 
}

.li-button:hover{
  background-color: rgba(255, 255, 255, 1.0);
  transform: scale(1.0); 
}

.bold-span{
  font-weight: bold;
}

.new-feature-button{
  background-color: rgba(184, 156, 45, 0.6);
  padding: 2px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  transition: all .2s ease-in-out; 
}

.new-feature-button:hover{
  background-color: rgba(184, 156, 45, 0.8);
  transform: scale(1.1); 
}

.input-hidden{
  background-color: transparent;
  border: none;
}

.county-state{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.county{
  width: 60%;
}

.state{
  width: 33%;
}

.input-hidden:focus{
  background-color: white;
}

.highlight{
  color: rgba(241, 241, 241, 1.0);
  transform: translateX(-15%);
}

.trash-icon{
  cursor: pointer;
  background-color: rgba(184, 156, 45, 0.8);
  display: inline-block;
  padding: 6px;
  border-radius: 50%;
  box-shadow: 0 0 3pt 2pt rgba(184, 156, 45, 1.0);
}

.icon-trash-lrg{
  color: rgba(184, 156, 45, 1.0);
  color: red;
}

.highlight2{
  color: rgb(155, 88, 25);
  transform: translate(-0%, -10%);
  cursor: pointer;
  /* transform: translateX(-20%); */
}

@media screen and (max-width: 768px) {
  
  /* * {
    background-color: rgba(241, 241, 241, 1.0);
  } */
  /* body{
    background-image: none;
  } */

  .welcome-container{
    width: 90vw;
    margin-top: 10vh;
  }

  .well-container{
    flex-direction: column;
  }

  .well-info{
    width: 90vw;
    margin-top: 2vh;
    margin-left: auto;
    margin-right: auto;
  }

  .canvas-frame{
    width: 90vw;
    margin-top: 2vh;
    margin-left: auto;
    margin-right: auto;
  }

  .login-form label{
    background: rgba(184, 156, 45, 0.3);
  }

}


